* {
  margin: 0;
  scroll-behavior: smooth;
}
body {
  cursor: url("./assets/cursor.png"), auto;
}
/* Chrome , Safari  , Edge , Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.greenColor {
  color: green;
}
.redColor {
  color: red;
}
